<template>
  <div class="baseCententContainer">
    <ls-card
      v-for="(card, key) in cardData"
      :key="key"
      :title="card.title"
      :reverse="key % 2 === 1"
    >
      <template v-slot:img>
        <ls-lazyLoad
          :src="card.imgPath"
          width="100%"
          height="100%"
        ></ls-lazyLoad>
      </template>
      <ls-card-mete>
        <template v-slot:dirs>
          {{ card.dirs }}
        </template>
      </ls-card-mete>
    </ls-card>
  </div>
</template>

<script>
export default {
  props: {
    cardData: {
      type: Array,
      required: true,
    },
  },
};
</script>